nav {
  background-color: #084277bc;
  color: #ffffff;
  padding: 5px 10px;
  font-size: medium;
}

ul {
  list-style: none;
  display: flex;
}

li {
  padding: 0 20px;
}

a {
  color: #ffffff;
  text-decoration: none;
}

main {
  padding: 20px;
}

ul.menu {
  list-style: none;
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

ul.menu > li {
  display: inline-block;
  padding: 10px 23px;
  text-align: center;
  cursor: pointer;
}

/* Style the sub-menus */
ul.submenu {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  background-color: grey;
}
ul.submenu > li {
  color: black;
  display: block;
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

/* Show sub-menus on hover or click */
ul.menu > li:hover ul.submenu {
  display: block;
}
