div {
  width: 400px;
  /* margin: 0 auto; */
  text-align: center;
}

h1 {
  font-size: 36px;
  color: #333;
  margin-bottom: 20px;
}

form {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #f7f7f7;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 30%;
  position: absolute;
  left: 0;
  right: 0;
}

label {
  display: block;
  margin-bottom: 10px;
  font-size: 18px;
  color: #555;
}

input[type="text"],
input[type="password"] {
  width: 90%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 18px;
}

button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  width: 150px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}
